<template>
  <b-row>
    <b-col>
      <div class="mb-4">
        <b-button @click="resetFilters" variant="primary" v-show="filtered" size="md">
           
        </b-button>
      </div>
      <b-table-simple
        :per-page="perPage"
        :current-page="currentPage"
        hover
        outlined
        responsive
        :fields="fields"
      >
        <b-thead head-variant="light">
          <b-tr>
            <b-th v-for="field in fields" :key="field.key">
              <template v-if="field.key === 'first_user'">
                {{ field.label }}
                <input
                  class='input'
                  :placeholder="field.label"
                  @input="getFilteredDeals(search)"
                  v-model="search.first_user_name"
                />
              </template>
              <template v-else-if="field.key === 'sec_user'">
                {{ field.label }}
                <input
                  class='input'
                  :placeholder="field.label"
                  @input="getFilteredDeals(search)"
                  v-model="search.sec_user_name"
                />
              </template>
              <template v-else-if="field.key === 'line'">
                {{ field.label }}
                <input
                  class='input'
                  :placeholder="field.label"
                  @input="getFilteredDeals(search)"
                  v-model="search.line_name"
                />
              </template>
              <template v-else-if="field.key === 'city'">
                {{ field.label }}
                <input
                  class='input'
                  v-model="search.city_name"
                  @input="getFilteredDeals(search)"
                  :placeholder="field.label"
                />
              </template>
              <template v-else-if="field.key === 'container'">
                {{ field.label }}
                <input
                  class='input'
                  v-model="search.container_name"
                  @input="getFilteredDeals(search)"
                  :placeholder="field.label"
                />
              </template>
              <template v-else-if="field.key === 'amount'">
                {{ field.label }}
                <input
                  class='input'
                  v-model="search.amount"
                  @input="getFilteredDeals(search)"
                  :placeholder="field.label"
                />
              </template>
              <template v-else-if="field.key === 'handshake'">
                {{ field.label }}
                <b-form-datepicker
                  locale="ru"
                  placeholder=""
                  :dateFormatOptions="{
                    day: 'numeric',
                    month: 'short',
                    year: 'numeric',
                  }"
                  @input="getFilteredDeals(search)"
                  size="sm"
                  v-model="search.handshake"
                  class="mb-2"
                ></b-form-datepicker>
                <b-form-datepicker
                  locale="ru"
                  placeholder=""
                  :dateFormatOptions="{
                    day: 'numeric',
                    month: 'short',
                    year: 'numeric',
                  }"
                  @input="getFilteredPropositions(search)"
                  size="sm"
                  v-model="search.handshake_end"
                  class="mb-2"
                ></b-form-datepicker>
              </template>
              <template v-else-if="field.key === 'actions'"> </template>
              <template v-else>
                {{ field.label }}
              </template>
            </b-th>
          </b-tr>
        </b-thead>
        <b-tbody>
          <b-tr v-for="item in lists" :key="item.id">
            <b-td v-for="field in fields" :key="field.key">
              <template v-if="field.key === 'first_user'">
                <b-link
                  :to="{
                    name: 'student-update',
                    params: { id: item[field.key].id },
                  }"
                  >{{ item[field.key].phone }}</b-link
                >
              </template>
              <template v-else-if="field.key === 'sec_user'">
                <b-link
                  :to="{
                    name: 'student-update',
                    params: { id: item[field.key].id },
                  }"
                  >{{ item[field.key].phone }}</b-link
                >
              </template>
              <template v-else-if="field.key === 'line'">
                {{ item[field.key].name }}
              </template>
              <template v-else-if="field.key === 'city'">
                {{ item[field.key].name }}
              </template>
              <template v-else-if="field.key === 'container'">
                {{ item[field.key].name }}
              </template>
              <template v-else-if="field.key === 'amount'">
                {{ item.amount.amount }}
              </template>
              <template v-else-if="field.key === 'handshake'">
                {{ item[field.key] }}
              </template>
              <template v-else-if="field.key === 'actions'">
                <div class="table__actions">
                  <b-button
                    class="btn_edit"
                    :to="{ name: 'deals-update', params: { id: item.id } }"
                  ></b-button>
                  <b-button
                    class="btn_delete"
                    @click="deleteUserDeal(item.id)"
                  />
                </div>
              </template>
              <template v-else>
                {{ item[field.key] }}
              </template>
            </b-td>
          </b-tr>
        </b-tbody>
      </b-table-simple>
      <b-pagination
        v-model="currentPage"
        :total-rows="rows"
        :per-page="perPage"
        aria-controls="item-table"
      ></b-pagination>
    </b-col>
  </b-row>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
  name: "UserRequestsList",
  data() {
    return {
      fields: [
        { key: "index", label: "#" },
        { key: "id", label: "ID" },
        { key: "first_user", label: " " },
        { key: "sec_user", label: " " },
        { key: "line", label: "" },
        { key: "city", label: "" },
        { key: "container", label: "" },
        { key: "amount", label: "-" },
        { key: "handshake", label: " " },

        { key: "actions", label: "" },
      ],
      activePage: 1,
      search: {
        first_user_name: "",
        sec_user_name: "",
        line_name: "",
        city_name: "",
        container_name: "",
        amount: "",
        handshake: "",
        handshake_end: "",
      },
      perPage: 1,
      currentPage: 1,
    };
  },
  computed: {
    ...mapState(["deals"]),
    rows() {
      return this.deals.list.length;
    },
    lists() {
      const items = this.deals.list;
      return items.slice(
        (this.currentPage - 1) * this.perPage,
        this.currentPage * this.perPage
      );
    },
  },
  watch:{
    search:{
      handler: function(a, b){
        console.log(a + ' ' + b)
            for(let key in this.search){
              if (this.search[key]){
                this.filtered = true
                break
              }   
            }
          },
      deep: true
    } 
  },
  created() {
    this.$store.state.breadcrumbs = [
      { text: "", to: { name: "home" } },
      { text: "", to: { name: "deals" } },
    ];

    this.getUserDeal().then((list) => {
      console.log(list);
    });
  },
  methods: {
    ...mapActions({
      saveUserDeal: "deals/saveItem",
      deleteUserDeal: "deals/deleteItem",
      getUserDeal: "deals/getList",
      getFilteredDeals: "deals/getFilteredItems",
    }),
    resetFilters(){
      this.filtered=false
      this.search = {
        first_user_name: "",
        sec_user_name: "",
        line_name: "",
        city_name: "",
        container_name: "",
        amount: "",
        handshake: "",
        handshake_end: "",
      }
      this.getUserDeal().then((list) => {
        console.log(list);
      });
	},
  },
};
</script>

<style scoped></style>
